



import React from 'react';
import { Row, Col, Card, Table, Tabs, Tab, Badge } from 'react-bootstrap';

import Loader from '../../App/layout/Loader/index'
import { getLedgerEntry } from '../../api/accounting/ledger';
import Jhilikka from '../../App/components/Reusables/Jhilikka';


const LedgerEntry = (props) => {


    const [loading, setLoading] = React.useState(true);
    const [ledgerEntryData, setLedgerEntryData] = React.useState(null);


    React.useEffect(() => {
        const controller = new AbortController();
        //Api call to get the data of user
        getLedgerEntry({ action: 'get', ledger_entry_id: props.match.params.id }, controller.signal)
            .then(async (res) => {
                console.log(res)
                if (res.json.status) {
                    const entry = res.json.ledger_entries[0];
                    await setLedgerEntryData({ ...entry })
                    setLoading(false)
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });
        return () => controller.abort();
    }, [])


    // const FreeEntriesList = freeEntriesArr.map((entry, index) => {
    //     return <tr key={index}>
    //         <th>{entry.id}</th>
    //         <td>Rs. {entry.amount}</td>
    //         <td>{entry.remarks} </td>
    //     </tr>
    // })

    // 
    if (loading) return <Loader />

    return (
        <>
            <Row className="d-flex justify-content-between  pageHead" >
                <h4 className="m-b-10" >Ledger Entry Details</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`/ledger-entry/update/${props.match.params.id}`)}
                    > <i className="feather icon-edit text-c-white f-15 m-r-5" />Update</button>

                </div>
            </Row>
            <Row>
                <Col xl={2}></Col>
                <Col xl={8}>
                    <Card>
                        <Card.Body>
                            <h5 className='mb-4'>Ledger Entry Details</h5>
                            <div className="row d-flex align-items-center">

                                <Col md={2}></Col>
                                <Col md={8}>
                                    <div className='mb-1 border-bottom'><b>Account Name</b></div>
                                    <div className='mt-1 mb-3'>
                                        {ledgerEntryData.account_str}{" "}
                                        {ledgerEntryData.is_active ? (<Badge variant="info">Active</Badge>) : (<Badge variant="secondary">Inactive</Badge>)}
                                    </div>

                                    <div className='mb-1 border-bottom'><b>Account Type</b></div>
                                    <div className='mt-1 mb-3'>{ledgerEntryData.account_type_str}</div>


                                    {/* <div className='mb-1 border-bottom'><b>Header</b></div>
                                    <div className='mt-1 mb-3'>{ledgerEntryData.header_str[0].toUpperCase() + ledgerEntryData.header_str.slice(1)}</div> */}

                                    <div className='mb-1 border-bottom'><b>Remarks</b></div>
                                    <div className='mt-1 mb-3'>{ledgerEntryData.remarks || 'No Remarks'}</div>

                                    <div className='mb-1 border-bottom'><b>Amount</b></div>
                                    <div className='mt-1 mb-3'>Rs. {ledgerEntryData.payment[0].amount}</div>

                                </Col>
                                <Col md={2}></Col>
                            </div>



                        </Card.Body>

                        {/* <Card.Body>
                            <Card.Header>
                                <Card.Title as="h5">Free Entries</Card.Title>
                            </Card.Header>
                            <Table responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Amount</th>
                                        <th>Remarks</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {FreeEntriesList}

                                </tbody>

                            </Table>

                        </Card.Body> */}
                    </Card>
                </Col>
                <Col xl={2}></Col>
            </Row>
        </>
    );
}

export default LedgerEntry;
